<template>
	
		<div class="page-loadmore">
			<Navbar title=''></Navbar>
			<div class="page-loadmore-wrapper" ref='wrapper' :style='{height:wrapperHeight+"px"}'>
				<mt-loadmore :bottom-method='loadBottom' @bottom-status-change='handleBottomChange' :bottom-all-loaded='allLoaded' ref='loadmore' :autoFill='false' :auto-fill='autoFill'>
				<ul class="page-loadmore-list">
					<li v-for='(item,index) in gList' :key='item.id'>
						<router-link :to='{name:"good.detail",params:{id:"goods_id"}}'>
							<img :src="item.img_url">
							<div class="title">{{item.title|controllShow(10)}}</div>
							<div class="desc">
								<div class="sell">
									<span>{{item.sell_price}}</span>
									<s>{{item.market_price}}</s>
								</div>
								<div class="detail">
									<div class="hot">
										
									</div>
									<div class="count">
										{{item.stock_quantity}}
									</div>
								</div>
							</div>
						</router-link>
					</li>
				</ul>	
			</mt-loadmore>
			</div>
		</div>
</template>

<script>
	export default{

		name:'GoodsList',

		data(){
			return{
				gList:[],
				allLoaded:false,
				page:1,
				wrapperHeight:0,
				autoFill:false
			};
		},
		methods:{
			//
			loadBottom(){
				console.log("");
				this.loadGoodsByPage();
				//
				this.$refs.loadmore.onBottomLoaded();
			},
			handleBottomChange(status){
				console.log(status);
			},
			loadGoodsByPage(){
				this.$axios.get('/static/goods/getgoods/pageindex='+this.page+'.json')
				.then(res=>{
					if(res.data.message.length<6){
						//
						this.$toast("");
						//
						this.allLoaded=true;
					}
					if(this.page==1){
						this.gList=res.data.message;
					}else{
						this.gList= this.gList.concat(res.data.message);
					}
					this.page++;
					
				}).catch(err=>{
					console.log("",err);
				})
			}
		},
		created(){
			//
			this.loadGoodsByPage();
		},
		mounted(){
			this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
		}

	};
</script>

<!-- mt-loadmore
1.bottomLoad
2.bottomLoad :bottom-all-loaded falsetrue
3.auto-filltruebottomload
4.pull 70pxdrop70pxloading
5.loadmoreonBottomLoaded()loadingpull
6.refjsthis.$reds.XXX
refjsthis$refs.XXXdom
7.
==> +  =  -->


<style lang="css" scoped="">
*{
	padding: 0;
	margin: 0;
}
.page-loadmore{
	width: 100%;
	margin-bottom: 100px;
}
.page-loadmore-wrapper{
	overflow: scroll;
	touch-action:auto;
}
.page-loadmore-list{
	width: 100%;
}
.page-loadmore-list li{
	width: 41%;
	float: left;
	margin-top: 5%;
	margin-left: 6%;
	border: 1px solid grey;
	padding-bottom: 5px;
}
.page-loadmore-list li img{
	width: 100%;
}
.title{
	font-size: 13px;
	font-weight: 600;
	margin-left: 2px;
	color: #000;
}
.sell{
	font-size: 13px;
}
.desc{
	width: 100%;
}
.sell span{
	font-size: 14px;
	color: red;
}
.sell s{
	font-size: 12px;
	margin-left: 8px;
	font-weight: 600;
	color: grey;
}
.detail{
	margin-top: 10px;
}

.hot{
	color: orange;
	float: left;
	font-size: 14px;
}
.count{
	color: orange;
	float: right;
	font-size: 14px;
}
</style>


