// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'
import Moment from 'moment'
import VuePreview from 'vue-preview'
import store from './store'
 
// defalut install
//
Vue.use(VuePreview);//Vue.component('vue-preview',{})

Vue.config.productionTip = false

// mint-ui 
import Mint from 'mint-ui';
Vue.use(Mint);
import 'mint-ui/lib/style.css';
/* eslint-disable no-new */

//URL
// Axios.defaults.baseURL = 'http://www.sinya.online/api/';
//axios,axiosvue
Vue.prototype.$axios = Axios;

//
import Navbar from '@/components/Common/Navbar.vue'
Vue.component(Navbar.name,Navbar)//

//
import MySwiper from '@/components/Common/MySwiper.vue'
Vue.component(MySwiper.name,MySwiper)//

import Comment from '@/components/Common/Comment.vue'
Vue.component(Comment.name,Comment)//

//moment
Vue.filter('converTime',function(data,formatStr){
	return Moment(data).format(formatStr);
});
Vue.filter('relativeTime',function(data){
	return Moment(data).fromNow();
})
//
Moment.locale('zh-cn');
//
Vue.filter('controllShow',function(str,num){
	//num
	if (str.length<=num) {
		return str;
	}
	//num0-num-1
	if(str.length>num){
		return str.substr(0,num-1)+'';
	}
});

// 
Axios.interceptors.request.use(function (config) {
    // 
    //
    Mint.Indicator.open({
    	text:''
    });
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

// 
Axios.interceptors.response.use(function (response) {
	//
	Mint.Indicator.close();
    // 
    return response;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

// busVue
// $bus
import EventBus from './EventBus.js'
Vue.prototype.$bus = EventBus;



//css
import '../static/css/global.css'

new Vue({
  el: '#app',
  router,
  store,//
  components: { App },
  template: '<App/>'
})



