<template>
	<div id="home">
		<!-- 4s -->
		<MySwiper url='/static/picture.json' myHeight='250px'></MySwiper>
		<!-- <mt-swipe :auto="3000" style='height:250px;' class='swiper'>
			<mt-swipe-item v-for = '(item,index) in imgs' :key='index'>
				<img :src="item.img" alt="">
			</mt-swipe-item>
		</mt-swipe> -->
		<!--  -->
		<div class="list">
			<ul>
				<li v-for = '(grid,index) in grids' :key='grid.id'>
					<a href="javascript:void(0);">
						<router-link :to="grid.router">
							<img :src="grid.src" class="image">
						</router-link>
					</a>	
					<p class="p">{{grid.title}}</p>
				</li>
			</ul>
		</div>

	</div>
</template>


<script>
// assetsvuetemplate/style../
// scriptimportstatic.vue../
	import src1 from '../../assets/news.png';
	import src2 from '../../assets/share.png';
	import src3 from '../../assets/show.png';
	import src4 from '../../assets/ask.png';
	import src5 from '../../assets/liuyan.png';
	import src6 from '../../assets/lianxi.png';
	var grids = [
		{id:1,src:src1,title:"",router:{name:'news.list'}},
		{id:2,src:src2,title:"",router:{name:'photo.list',params:{CategoryId:0}}},
		{id:3,src:src3,title:"",router:{name:'good.list',params:{page:1}}},
		{id:4,src:src4,title:"",router:{name:'news.list'}},
		{id:5,src:src5,title:"",router:{name:'news.list'}},
		{id:6,src:src6,title:"",router:{name:'news.list'}},
		// {id:7,src:src1,title:""},
		// {id:8,src:src2,title:""},
		// {id:9,src:src3,title:""},
		// {id:10,src:src4,title:""},
		// {id:11,src:src5,title:""},
		// {id:12,src:src6,title:""}
	]
	export default{

		name:'Home',
		data(){
			return{
				imgs:[],
				grids : grids,
			};
		},
		created(){
			// this.$axios.get('getlunbo')
			// .then(res=>{
			// 	console.log(res.data);
			// 	this.imgs = res.data.message;
			// })
			// .catch(err=>{
			// 	console.log('',err);
			// })
			// this.$axios.get('/static/picture.json')
			// .then(res=>{
			// 	this.imgs = res.data.message;
			// })
			// .catch(err=>{
			// 	console.log("",err);
			// })
		}
	};
</script>

<!-- scoped -->
<style lang="css" scoped>
.swiper img{
	width: 100%;
	height: 250px;
}
.list{
	width: 100%;
}
.list ul{
	display: flex;
	flex-wrap: wrap;
	margin-bottom:50px;
	padding-bottom: 50px;
}
.list ul li{
	width: 33%;
	height: 60px;
	text-align: center;
	font-size: 13px;
	margin-top: 15px;
}
.list ul li a{
	display: block;
	width: 30px;
	height: 30px;
	margin: 0 auto;
}
.image{
	height: 30px;
	width:30px;
}
.p{
	font-weight: bold;
	margin-top: 10px;
}
</style>


