<template>
	<div class="photoList">
		
		<Navbar title=''></Navbar>
		<div class="category-list">
			<ul>
				<li v-for='(category,index) in categoryList' :key='category.id' @click='categoryHandler(category.id,index)'>
					<a href="javascript:void(0);" :class='{active:index==currentIndex}'>{{category.title}}</a>
				</li>
			</ul>
		</div>
		<!--  -->
		<div class="photo-list">
			<ul>
				<li v-for='(item,index) in imgList' :key='item.id'>
					<router-link :to="{name:'photo.detail',query:{id:item.id}}">
						<!-- <img :src="item.img_url"> -->
						<!-- mint-ui -->
						<img v-lazy="item.img_url">
					</router-link>
					<p>
						<span>{{item.title}}</span>
						<br>
						<span>{{item.zhaiyao}}</span>
					</p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{

		name:'PhotoList',

		data(){
			return{
				categoryList:[],
				imgList:[],
				currentIndex:0
			};
		},
		methods:{
			categoryHandler(id,index){
				//url
				this.$router.push({name:'photo.list',params:{CategoryId:id}})
				this.currentIndex=index;
			},
			loadImgByCategoryId(id){
				this.$axios.get('/static/message/tuwenDetail'+id+'.json')
				.then(res=>{
					this.imgList=res.data.message;
					if(this.imgList==null){
						this.$toast({
						  message: '',
						  position: 'center',
						  duration: 1000
						});
					}
				}).catch(err=>{
					console.log('',err);
				})
			}
		},
		beforeRouteEnter (to, from, next) {
		    //  confirm 
		    //  `this`
		    // 
		    next(vm=>{
		    	vm.loadImgByCategoryId(to.params.CategoryId)
		    })
		},
		//
		beforeRouteUpdate (to, from, next) {
		    // 
		    //  /foo/:id /foo/1  /foo/2 
		    //  Foo 
		    //  `this`
		    this.loadImgByCategoryId(to.params.CategoryId);
		    next();
		},
		created(){
			// this.loadImgByCategoryId(0);
			this.$axios.get('/static/categoryList.json')
			.then(res=>{
				this.categoryList = res.data.message;
				this.categoryList.unshift({"id":0,"title":""});
			}).catch(err=>{
				console.log("",err);
			})
		}
	};
</script>

<style lang="css" scoped="">
.category-list{
	width: 100%;
/*	height: 40px;*/
	overflow: hidden;
}
.category-list ul{
	width: 100%;
/*	height: 50px;*/
	overflow-x: scroll;
	overflow-y: hidden;
	white-space: nowrap;
}
.category-list ul li{
	display: inline-block;
	width: 25%;
	height: 100%;
	font-size: 18px;
	line-height: 40px;
	text-align: center;
}
.category-list ul li a{
	text-decoration: none;
	font-size: 16px;
}
.category-list ul li a.active{
	color: #FC0280;
}

.photo-list{
	width: 100%;
}
.photo-list ul{
	width: 100%;
}
.photo-list ul li{
	width: 100%;
	display: block;
	position: relative;
}
.photo-list ul li img{
	width: 100%;
}
.photo-list ul p{
	width: 100%;
	position: absolute;
	bottom: 10px;

}
.photo-list ul li p span{
	color: white;
	font-size: 14px;
	background-color: rgba(0,0,0,.5);
}
.photoList{
	padding-bottom: 100px;
}
image[lazy=loading] {
  width: 40px;
  height: 300px;
  margin: auto;
}
/*





.icon-meiyoutupian{
	width: 50px;
	height: 50px;
}*/
</style>


