<template>
	<div>
		<Navbar title=''></Navbar>
		<div class="pay-detail">
			<ul>
				<li class="p-list" v-for="(shop,index) in shopCar" :key='shop.id'>
					<mt-switch v-model='shop.idSelect'></mt-switch>
					<img :src="shop.thumb_path">
					<div class="pay-calc">
						<p>{{shop.title}}</p>
						<div class="calc">
							<span class="s1">{{shop.sell_price}}</span>
							<span class="s2" @click='substract(shop)'>-</span><span class="s3">{{shop.num}}</span><span class="s4" @click='addnum(shop)'>+</span>
							<a href="javascript:void(0);" @click='del(shop,index)'></a>
						</div>
					</div>	
				</li>
			</ul>
		</div>
		<div class="show-price">
			<div class="show-1">
				<p>()</p>
				<span>{{payment.count}}{{payment.total}}</span>
			</div>	
			<div class="show-2">
				<mt-button type='danger' size='large'></mt-button>
			</div>
		</div>
	</div>
</template>

<script>
import GoodsTool from '@/GoodsTool'
	export default{
		name:'Cart',

		data(){
			return{
				shopCar:[],
			};
		},
		methods:{
			substract(shop){
				if (shop.num==0) {
					return;
				}
				shop.num--;
				this.$store.dispatch('addShopNumByAction',-1);
			},
			addnum(shop){
				shop.num++;
				this.$store.dispatch('addShopNumByAction',1);
			},
			del(shop,index){
				//
				this.shopCar.splice(index,1);
				//shop
				// delete GoodsTool[shop.id];
				GoodsTool.removeGoods(shop.id);
				localStorage.clear();
				let num = shop.num;
				this.$store.dispatch('addShopNumByAction',-num);
			}
		},
		beforeRouteLeave(to,from,next){
			//
			//'this'
			let res=confirm('');
			if (res) {
				//
				let obj={};
				this.shopCar.forEach(shop=>{
					obj[shop.id]=shop.num;
				})
				GoodsTool.saveGoods(obj);
				next();
			}else{
				next(false);//
			}
		},
		computed:{
			payment(){
				//getter
				let total=0;//
				let count=0;//
				this.shopCar.forEach(shop=>{
					if(shop.idSelect){
						//
						count+=shop.num;
						total+=shop.num*shop.sell_price
					}
				});
				return{
					total,count,
				};
			}
		},
		created(){
			console.log(GoodsTool.getGoodsList());
			let goodsList = GoodsTool.getGoodsList();
			//{{"101":5}}
			let ids=Object.keys(goodsList).join(',');
			if (ids) {
				// 
				this.$axios.get("/static/goods/shopcart"+ids+".json")
				.then(res=>{
					this.shopCar=res.data.message;
					//vueshopCart
					// Object.defineProperty(this,'shopCart',{
					// 	set:function(){
					// 		shopCart
					// 	}
					// })shopCart


					//
					for(var i=0;i<this.shopCar.length;i++){
						let shop = this.shopCar[i];
						let num = goodsList[shop.id];
						if (num) {
							// vue==>
							//
							//
							this.$set(shop,'num',num);
							this.$set(shop,'isSelect',true);
							shop.num = num;
							shop.isSelect=true;
						}
					}
				}).catch(err=>{
					console.log("",err);
				})
			}
		}
	};
</script>

<style lang="css" scoped="">
.p-list{
	width: 100%;
}
.p-list img{
	width: 100px;
	height: 100px;
}
.pay-calc	p{
	font-size: 18px;
	font-weight: bold;
	margin-top: 10px;
	margin-bottom: 10px;
	color: blue;
}	
.s1{
	font-weight: bold;
	margin-right: 15px;
}	
.s2{
	display: inline-block;
	border:1px solid grey;
	text-align: center;
	width: 15px;
}
.s3{
	border:1px solid grey;
	display: inline-block;
	text-align: center;
	width: 25px;
}
.s4{
	border:1px solid grey;
	display: inline-block;
	width: 15px;
	text-align: center;
}
.calc a{
	margin-left: 15px;
}
.show-price{
	margin-top: 20px;
	background: grey;

}
.show-1{
	font-size: 17px;
	margin-left: 10px;
}
</style>


